<template>
	<view class="background fix-padding">
		<!-- 搜索 -->
		<view class="container search">
			<view class="flex">
				<uni-easyinput class="input" v-model="search_name" placeholder="请输入客户名称"></uni-easyinput>
				<button class="btn-search" @click="search">搜索</button>
			</view>
			<view class="flex mt-20">
				<view class="text">自定义查</view>
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="start_time" type="date" placeholder="开始日期"/>
				至
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="end_time" type="date" placeholder="结束日期"/>
				<button class="btn-search btn-color" @click="searchTime">查询</button>
			</view>
			<view class="flex mt-20">
				<picker class="picker" @change="changeSale" :value="sIndex" :range="Sale_list" :range-key="'username'">
					<view v-if="sIndex == -1" class="uni-input">选择销售</view>
					<view v-else class="uni-input">{{Sale_list[sIndex].username}}</view>
					<image class="image" src="/static/image/picker.png"></image>
				</picker>
			</view>
		</view>
		<!-- 列表 -->
		<view class="container list">
			<view class="item" v-for="(item,index) in list" :key="item.id">
				<view class="big ellipsis">
					订单号{{item.order_number}}
				</view>
				<view class="flex top .mb-10">
					<view class="title grey">客户名称</view>
					<view class="value">{{item.name}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">销售员</view>
					<view class="value">{{item.username}}</view>
				</view>
				<view class="flex .mb-10">
					<view class="title grey">订货日期</view>
					<view class="value">{{item.order_date}}</view>
				</view>
				<view class="flex .mb-10 last">
					<view class="title grey">备注内容</view>
					<view class="value">{{item.remarks}}</view>
				</view>
				<view class="arrow flex flex-column" @click="detail(item.id)">
					<view>详情</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				search_name:'',
				start_time:'',
				end_time:'',
				list:[],
				page:0,
				sIndex:-1,
				Sale_list:[],
				create_user_id:'',
			}
		},
		onLoad() {
			this.page = 0
			// 出库记录接口
			this.listInterface()
			// 销售
			this.http({
				url: this.api.getUserData,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					
				},
				success: res => {
					this.Sale_list = res
					let item = {
						id:'',
						username:"全部",
					}
					this.Sale_list.unshift(item)
				}
			})
		},
		//上拉触底函数
		onReachBottom(){
			// 列表接口
			this.http({
				url: this.api.Order,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					search: this.search_name,
					order_date_start:this.start_time,
					order_date_end:this.end_time,
					create_user_id:this.create_user_id,
					page:this.page,
					type:'row'
				},
				success: res => {
					this.page+=1
					if(res.list.length == 0) {
						uni.showToast({
							title:'没有更多了',
							icon:'none',
							duration: 2000
						})
					} else {
						this.list = this.list.concat(res.list)
					}
				}, 
			})
		},
		methods: {
			// 列表接口
			listInterface() {
				this.http({
					url: this.api.Order,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						search:this.search_name,
						order_date_start:this.start_time,
						order_date_end:this.end_time,
						create_user_id:this.create_user_id,
						page:this.page,
						type:'row'
					},
					success: res => {
						this.page+=1
						if(res.list.length == 0) {
							uni.showToast({
								title:'没有数据',
								icon:'none',
								duration: 2000
							})
							this.list = res.list
						} else {
							this.list = res.list
						}
					}
				})
			},
			// 搜索 查询按钮点击
			search() {
				this.page = 0
				this.listInterface()
			},
			// 销售点击
			changeSale(e) {
				this.page = 0
				this.sIndex = e.target.value
				this.create_user_id = this.Sale_list[this.sIndex].id
				this.listInterface()
			},
			// 时间
			searchTime() {
				this.page = 0
				this.listInterface()
			},
			// 详情按钮点击
			detail(eve){
				uni.navigateTo({
					url:'/pages/ruler/details/details?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
		}
	}
</script>

<style lang="scss">
.btn-color {
	width: 120rpx!important;
	height: 64rpx;
	line-height: 64rpx;
	background: $color-b5;
}
.search .uni-date-editor--x .uni-date__icon-clear {
	top: -4rpx;
}
.uni-date-single {
	padding: 0!important;
	height: 64rpx;
}
.date-picker {
	width: 42%!important;
}
.uniui-calendar {
	display: none;
}
</style>
